	module.exports = {"data":{"markdownRemark":{"html":"<h2>How to Create a Countdown Timer</h2>\n<h3>Creation</h3>\n<p>Start by building the countdownTimer function.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token keyword\">function</span> <span class=\"token function\">startCountdown</span><span class=\"token punctuation\">(</span>seconds<span class=\"token punctuation\">)</span><span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">var</span> counter <span class=\"token operator\">=</span> seconds<span class=\"token punctuation\">;</span>\n\n  <span class=\"token keyword\">var</span> interval <span class=\"token operator\">=</span> <span class=\"token function\">setInterval</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n    console<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span>counter<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    counter<span class=\"token operator\">--</span><span class=\"token punctuation\">;</span>\n    \n\n    <span class=\"token keyword\">if</span><span class=\"token punctuation\">(</span>counter <span class=\"token operator\">&lt;</span> <span class=\"token number\">0</span> <span class=\"token punctuation\">)</span><span class=\"token punctuation\">{</span>\n      \n      <span class=\"token comment\" spellcheck=\"true\">// The code here will run when</span>\n      <span class=\"token comment\" spellcheck=\"true\">// the timer has reached zero.</span>\n      \n      <span class=\"token function\">clearInterval</span><span class=\"token punctuation\">(</span>interval<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n      console<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span><span class=\"token string\">'Ding!'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token punctuation\">}</span><span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span> <span class=\"token number\">1000</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<h3>Execution</h3>\n<p>Now to start the timer we provide <code>startCountdown()</code> with a number value as an argument which represents the seconds to countdown.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>  <span class=\"token function\">startCountdown</span><span class=\"token punctuation\">(</span><span class=\"token number\">10</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token comment\" spellcheck=\"true\">// Console Output // </span>\n  <span class=\"token comment\" spellcheck=\"true\">// 10</span>\n  <span class=\"token comment\" spellcheck=\"true\">// 9</span>\n  <span class=\"token comment\" spellcheck=\"true\">// 8</span>\n  <span class=\"token comment\" spellcheck=\"true\">// 7</span>\n  <span class=\"token comment\" spellcheck=\"true\">// 6</span>\n  <span class=\"token comment\" spellcheck=\"true\">// 5</span>\n  <span class=\"token comment\" spellcheck=\"true\">// 4</span>\n  <span class=\"token comment\" spellcheck=\"true\">// 3</span>\n  <span class=\"token comment\" spellcheck=\"true\">// 2</span>\n  <span class=\"token comment\" spellcheck=\"true\">// 1</span>\n  <span class=\"token comment\" spellcheck=\"true\">// 0 </span>\n  <span class=\"token comment\" spellcheck=\"true\">// Ding!</span>\n</code></pre>\n      </div>\n<h3>Live Example</h3>\n<p><a href='https://codepen.io/rdev-rocks/pen/jLogxY?editors=0012' target='_blank' rel='nofollow'>Codepen - Countdown Timer</a></p>\n<h3>More Resources</h3>\n<p>Methods used:</p>\n<ul>\n<li><a href='https://developer.mozilla.org/en-US/docs/Web/API/WindowOrWorkerGlobalScope/setInterval' target='_blank' rel='nofollow'>setInterval()</a></li>\n<li><a href='https://developer.mozilla.org/en-US/docs/Web/API/WindowOrWorkerGlobalScope/clearInterval' target='_blank' rel='nofollow'>clearInterval()</a></li>\n</ul>","fields":{"slug":"/javascript/tutorials/how-to-create-a-countdown-timer/"},"frontmatter":{"title":"How to Create a Countdown Timer"}}},"pathContext":{"slug":"/javascript/tutorials/how-to-create-a-countdown-timer/"}}

